@use '../../../bpk-mixins/tokens';
@use '../layoutMixins';

// Base BpkContainer class
.bpk-container {
  width: 100%;
  max-width: 75rem; // Default max-width (1200px), can be overridden
  margin-right: auto;
  margin-left: auto;
  @include layoutMixins.base-component-styles;
}

// Common layout utilities (spacing, color, layout)
@include layoutMixins.common-layout-utilities($include-gap: false, $include-border-color: false);
